import Vue from 'vue'
import App from './App'
import {router,addRouterInterceptor,checkAuth,checkIsAdmin} from './router'
import ElementUi from 'element-ui'
import 'element-ui/lib/theme-chalk/index.css'
import {urlConf,ajax,addAjaxInterceptors,changeAjaxBaseUrl} from './api'
import VueCookies from 'vue-cookies'
import store from './store/index'

Vue.component('xm-tags',()=>import('./components/tools/xm-tags'))
Vue.component('xm-info-box',()=>import('./components/tools/xm-info-box'))

Vue.config.productionTip = false
Vue.use(ElementUi)
Vue.use(VueCookies)

Vue.prototype.$ajax = ajax
Vue.prototype.$urlConf = urlConf

const vue = new Vue({
  router,
  store,
  components: { App },
  template: '<App/>'
})

Vue.prototype.checkAuth = () => checkAuth(vue)
Vue.prototype.updateBaseUrl = (prefix) => {
  changeAjaxBaseUrl(prefix)
  vue.$cookies.set('curUrlPrefix',prefix)
}

addAjaxInterceptors(vue)

changeAjaxBaseUrl(vue.$cookies.get('curUrlPrefix'))

checkIsAdmin(vue).then(()=>{
  vue.$store.commit('updateIsAdmin',true)
}).catch(()=>{
  vue.$store.commit('updateIsAdmin',false)
})

// addRouterInterceptor()

// 最后再绑定页面,确保前面所有操作已经完成后,再加载页面
vue.$mount('#app')



