import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

export const menuDict = [
  {
    title:'帮助手册',
    icon:'el-icon-notebook-2',
    path: '/plugins-dict',
    name: 'plugins-dict',
    component: () => import('../components/others/plugins-dict')
  },
  {
    title:'复读设置',
    icon:'el-icon-printer',
    path: '/repeat-setting',
    name: 'repeat-setting',
    component: () => import('../components/repeat/repeat-setting')
  },
  {
    title:'Chat设置',
    icon:'el-icon-chat-round',
    path: '/chat-setting',
    name: 'chat-setting',
    component: () => import('../components/chat/chat-setting')
  },
  {
    title:'RSS设置',
    icon:'el-icon-paperclip',
    path: '/rss-setting',
    name: 'rss-setting',
    component: () => import('../components/rss/rss-setting')
  },
  {
    title:'Live设置',
    icon:'el-icon-video-play',
    path: '/live-setting',
    name: 'live-setting',
    meta: {auth: true},
    component: () => import('../components/live/live-setting')
  },
  {
    title:'PCR日历设置',
    icon:'el-icon-date',
    path: '/calendar-setting',
    name: 'calendar-setting',
    component: () => import('../components/calendar/pcr-calendar')
  },
  {
    title:'PCR卡池设置',
    icon:'el-icon-s-custom',
    path: '/gacha/pcr/pool-setting',
    name: 'pcr-pool-setting',
    meta: {auth: false},
    component: () => import('../components/gacha/pcr-pool-setting')
  }
]

const routes = [
  {
    path: '/',
    redirect: '/index'
  },
  {
    path: '*',
    redirect: '/404'
  },
  {
    path: '/404',
    name: '404',
    component: () => import('../components/404')
  },
  {
    path: '/index',
    name: 'Index',
    component: () => import('../components/index'),
    redirect: '/home',
    children:[
      {
        title:'首页',
        icon:'',
        path: '/home',
        name: 'home',
        component: () => import('../components/home')
      },...menuDict]
  },
  {
    path: '/login',
    name: 'login',
    component: () => import('../components/admin/login')
  },
  {
    path: '/reset-pwd',
    name: 'resetPassword',
    component: () => import('../components/admin/reset-pwd')
  }
]

export const router = new Router({
  routes: routes
})

export function addRouterInterceptor() {
  router.beforeEach((to, from, next) => {
    if (to.matched.some(m => m.meta && m.meta.auth)) {
      checkAuth().then(() => next()).catch(err => next(err))
    } else {
      next()
    }
  })
}

export function checkAuth(vue) {
  return new Promise((resolve, reject) => {
    let token = vue.$cookies.get('token')
    let salt = vue.$cookies.get('salt')
    if (token && salt) {
      vue.$ajax({
        url: '/token.valid',
        params: {token: token, salt: salt}
      }).then(res => {
        if (res['code'] === 0) {
          resolve()
        } else {
          vue.$message({
            type: 'error',
            message: res['msg'],
            duration: 1000,
            onClose: () => {
              reject('/login')
            }
          })
        }
      }).catch(e => {
        reject('/index')
      })
    } else {
      vue.$message({
        type: 'error',
        message: '未登录',
        duration: 1000,
        onClose: () => {
          reject('/login')
        }
      })
    }
  })
}

export function checkIsAdmin(vue, userId) {
  return new Promise((resolve, reject) => {
    let user_id = userId ? userId : vue.$cookies.get('user_id')
    if (user_id) {
      vue.$ajax({url: '/checkIsAdmin.valid', params: {user_id: user_id}})
        .then(res => {
          if (res['code'] === 0) {
            resolve()
          } else reject()
        })
        .catch(err => {
          reject()
        })
    } else
      reject()
  })
}



