import Vuex from 'vuex'
import Vue from 'vue'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    isAdmin: false,
    calendar_setting: {}
  },
  mutations: {
    updateIsAdmin(state, value) {
      state.isAdmin = value
    },
    updateCalendar(state, value){
      state.calendar_setting = value
    }
  }
})



